import React, {useState} from 'react'
import "./CartItem.css"
import {removeFromCart, adjustItemQty} from "../../../redux/Shoping/shopping-actions"
import { connect } from 'react-redux';

const CartItem = ({itemData, removeFromCart, adjustItemQty}) => {
    const [input, setInput] = useState(itemData.qty);
    if(input < 0) return setInput(0);

    const addQty = () => {
        setInput(Number(input + 1));
        adjustItemQty(itemData.id, input+1);
      };
    
    const removeQty = ()=>{
        setInput(Number(input - 1));
        adjustItemQty(itemData.id, input-1);
    }

    return (
        <div className="CartItem">
            <img src={itemData.img}/>
            <h2>{itemData.title}</h2>
            <div>
                <span>{itemData.price} руб.</span>
                <div className="number">
                    <button onClick={addQty}>+</button>
                    <h5>
                    {input}
                    </h5>
                    <button onClick={removeQty}>-</button>
                </div>
            </div>
            <button className="back" onClick={() => removeFromCart(itemData.id)}>Удалить ❯</button>
            
        </div>
    )
}

const mapDispatchToProps = (dispatch) =>{
    return {
        removeFromCart: (id) => dispatch(removeFromCart(id)),
        adjustItemQty: (id, value) => dispatch(adjustItemQty(id,value))
    }
}

export default connect(null, mapDispatchToProps)(CartItem);