import React from 'react'
import "./Product.css"
import { connect } from 'react-redux';
import{ addToCart, loadCurrentItem } from '../../../redux/Shoping/shopping-actions'
import { Link } from 'react-router-dom';

function Product({productData, addToCart, loadCurrentItem}) {
    return (
        <div className="product" onClick={() => addToCart(productData.id)}>
            <img src={productData.img}/>
            <div className="prodInfo">
                <h2>{productData.title}</h2>
                <span>{productData.price} руб.</span>
                <br/>
                <Link to={`/product/${productData.id}`}>
                    <button className="link" onClick={() => loadCurrentItem(productData)}>View Item</button>
                </Link>
            </div>
        </div>
    )
}

const mapDispatchToProps = dispatch =>{
    return {
        addToCart: (id) => dispatch(addToCart(id)),
        loadCurrentItem: (item) => dispatch(loadCurrentItem(item))
    }
}

export default connect(null,mapDispatchToProps)(Product);