import React, {useEffect, useState} from 'react'
import { Link } from 'react-router-dom'
import "./Cart.css"
import CartItem from './CartItem/CartItem'

import { connect } from 'react-redux'

function Cart({cart}) {
    const [totalPrice, setTotalPrice] =useState(0);
    const [TotalItems, setTotalItems] =useState(0);

    useEffect(()=>{
        let items = 0;
        let price =0;
        cart.forEach(item => {
            items += item.qty;
            price += item.qty * item.price
        })
        setTotalPrice(price);
        setTotalItems(items);
    }, [cart, totalPrice, TotalItems, setTotalItems, setTotalPrice])

    if(TotalItems==0){return(
        <div className="container">
        <h1>Ваша корзина пуста.</h1>
        <div className="cart-products">
        <span style={{fontSize:'20px'}}> Добавьте что-нибудь из каталога </span>
            <div>
                <h3>Итого к оплате:</h3>
                <h2>{totalPrice} руб.</h2>
                <br/>
                <button className="notActive">Оплатить</button>
                <Link to="/">
                <button className="back">Продолжить покупки ❯</button>
                </Link>
            </div>
        </div>
    </div>
    )}else{
    return (
        <div className="container">
            <h1>Корзина.</h1>
            <div className="cart-products">
            <div className="cart-render">
               {
                 cart.map((item) => <CartItem key={item.id} itemData={item}/>)  
               }
               </div>
                <div>
                    <h3>Итого к оплате:</h3>
                    <h2>{totalPrice} руб.</h2>
                    <br/>
                    <button onClick={() => alert(JSON.stringify(cart))}>Оплатить</button>
                    <Link to="/">
                    <button className="back">Продолжить покупки ❯</button>
                    </Link>
                </div>
            </div>
        </div>
    )
            }
};

const mapStateToProps = state => {
    return {
        cart: state.shop.cart,
    };
}


export default connect(mapStateToProps)(Cart);
