import * as actionTypes from './shopping-types';
const INITIAL_STATE ={
    products: [
        {
            id:1,
            title: 'Honor MagicBook X ',
            price: '100000',
            img :"https://geek-brand.ru/image/cache/catalog/revolution/phone/brandhonor/32-800x800.jpg",
        },
        {
            id:2,
            title: 'MateBook',
            price: '69000',
            img:'https://cdn1.ozone.ru/s3/multimedia-3/6026271039.jpg'
        },
        {
            id:3,
            title: 'Apple MacBook Pro',
            price: '120000',
            img :"https://www.iport.ru/upload/files/product/macbook_pro_silver_2_0.jpg",
        },
        {
            id:4,
            title: 'ASUS Zenbook',
            price: '57000',
            img :"https://cdn.kns.ru/linkpics/asus-zenbook-14-ux431fa-am187r-90nb0mb3-m05330-0-v2.jpg",
        },
        {
            id:5,
            title: 'Microsoft Surface Laptop Go',
            price: '55000',
            img :"https://i.expansys.net/img/p/314224/microsoft-surface-laptop.jpg",
        },
        {
            id:6,
            title: 'Apple MacBook Air 13',
            price: '220000',
            img :"https://img.mvideo.ru/Pdb/30044904b.jpg",
        },
    ],
    cart: [],
    currentItem: null,
}


const shopReducer = (state= INITIAL_STATE, action) =>{
    switch(action.type){
        case actionTypes.ADD_TO_CART:
                const item = state.products.find((product)=> product.id === action.payload.id)
                const isInCart = state.cart.find(item=> item.id === action.payload.id ? true : false);
            return{
                ...state,cart:isInCart ? state.cart.map((item)=> item.id === action.payload.id ? {...item, qty:item.qty +1} : item) : [...state.cart, {...item, qty:1}],
            }
        case actionTypes.REMOVE_FROM_CART:
            return{
                ...state,
                cart: state.cart.filter(item => item.id !== action.payload.id ),
            }
        case actionTypes.ADJUST_QTY:
            return{
                ...state,
                cart: state.cart.map(item => item.id === action.payload.id ? {...item, qty: +action.payload.qty}:item)
            }
        case actionTypes.LOAD_CURRENT_ITEM:
            return{
                ...state,
                    currentItem: action.payload,
            }
        default:
            return state;
    }
};

export default shopReducer;